.settings {
    margin-top: 15px;
    font-family: "Courier New", monospace;
    font-weight: bold;
    font-size: 18px;
    padding: 20px;
    width: 50%;
    height: 75vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    border: 3px solid #fff;
    background-color: #333;
    color: #fff;
}

.settings-container {
    width: 50%;
    height: 65%;
    border: 3px solid #fff;
    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
}
  
.settings h1 {
    font-size: 2.25em;
    margin-bottom: 10px;
    text-align: center;
}

.difficulty-container {
    display: flex;
}

.settings-subcontainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sound-options-container {
    display: flex;
}

.back-button-container {
    width: 90%;
    display: flex;
    justify-content: flex-end;
}

.settings-button {
    font-family: "Courier New", monospace;
    font-size: 18px;
    background-color: #000;
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    margin-right: 10px;
    font-weight: bold;
    border: 3px solid #fff;
}

.selected {
    background-color:#fff;
    color: #000;
}

.back {
    margin-bottom: 15px;
}

@media screen and (max-width: 500px) {
    .settings {
      width: 90vw;
      height: 85vh;
    }

    .settings-container {
        width: 95vw;
        height: 85%;   
    }

}
  


